<template>
  <div class="share_part_wrap flex-ra" v-if="detailType=='kcz' || content.can_share == 1">
    <div class="share flex-cc" @click='handleShare(v)' v-for='(v,k) in shareOptions' :key='k'>
      <img :src="v.src" />
    </div>
  </div>
</template> 
<script>
/**分享 */
import { Toast } from "vant";
import { fetchClientFunction } from "@/utils/interface";
export default {
  name: "sharePart",
  props: {
    detailType: {
      type: String,
      default: "kcz",
    },
    content: {
      type: Object,
      default: {
        can_share: 1, //常州号 文稿详情
      },
    },
  },

  data() {
    return {
      shareOptions: [
        {
          src: require("@/assets/images/weixin.png"),
          params: "weixin",
        },
        {
          src: require("@/assets/images/pengyouquan.png"),
          params: "pengyouquan",
        },
        {
          src: require("@/assets/images/weibo.png"),
          params: "weibo",
        },
      ],
    };
  },


  methods: {
    launchData() {
      // infoData
      let data = {
        type: "infoData",
        params: JSON.stringify(this.content),
      };
      fetchClientFunction(data);
      console.log("created infoData",this.content)

    },

    handleShare(record) {
      const { params } = record;
      const data = {
        type: "share",
        params,
      };
      fetchClientFunction(data);
    },
  },
};
</script>

<style lang="scss"  src="./index.scss" scoped></style>


